/* eslint-disable */
// CSS
import "../sass/index.sass";
// JS
// import Stats from "stats.js";
// require('aframe');
require('aframe-gltf-part-component');
require('aframe-orbit-controls');
// require('aframe-physics-system');
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
// import CannonDebugger from 'cannon-es-debugger';
import * as CANNON from "cannon-es";
import CannonUtils from "cannon-es/utils/cannonUtils";
import { RGBELoader } from "three/examples/jsm/loaders/RGBELoader";
import { EffectComposer } from "three/examples/jsm/postprocessing/EffectComposer";
import { RenderPass } from "three/examples/jsm/postprocessing/RenderPass";
import { UnrealBloomPass } from "three/examples/jsm/postprocessing/UnrealBloomPass";
import { OutputPass } from "three/examples/jsm/postprocessing/OutputPass";
// import { GTAOPass } from "three/examples/jsm/postprocessing/GTAOPass";
// import { SSAOPass } from "three/examples/jsm/postprocessing/SSAOPass";
// import { DotScreenPass } from "three/examples/jsm/postprocessing/DotScreenPass";
// import { BokehPass } from 'three/examples/jsm/postprocessing/BokehPass';
import * as BIG from './big';

// import { GUI } from 'three/examples/jsm/libs/lil-gui.module.min';

//

document.addEventListener('DOMContentLoaded', function() {
  const sceneEl = document.querySelector('a-scene');
  sceneEl.setAttribute('load-complete', '');
});

// Stats
// const stats = new Stats();
// const statsContainer = document.getElementById('stats-container');
// statsContainer.appendChild(stats.dom);

//3D
AFRAME.registerComponent('load-complete', {
    init: function() {
      // 
      let controlMode = "QMIMI"; // QMIMI, CAR
      // SCENE
      const sceneEl = document.querySelector('#scene');
      new RGBELoader().load("images/bigcity.hdr", function(texture){
        texture.mapping = THREE.EquirectangularReflectionMapping;
        texture.image.data = texture.image.data.map(value => value * 1);
				sceneEl.object3D.environment = texture;
      });

      let sequinArray = [];

      // CAMERA
      const basicCamera = document.querySelector('#basic_camera');
      
      // Bloom
      const renderScene = new RenderPass( sceneEl.object3D, sceneEl.camera );

      const bloomPass = new UnrealBloomPass( new THREE.Vector2( window.innerWidth, window.innerHeight ), 1.5, 0.4, 0.85 );
			bloomPass.threshold = 1;
			bloomPass.strength = 0.33;
			bloomPass.radius = 0;

      const outputPass = new OutputPass();
      const renderer = sceneEl.renderer;
      const render = renderer.render;

      // const bokehPass = new BokehPass( sceneEl.object3D, sceneEl.camera, {
      //   focus: 1,
      //   aperture: 1,
      //   maxblur: 0.01
      // } );

      // const dotScreenPass = new DotScreenPass();

      // const gtaoPass = new GTAOPass( sceneEl.object3D, sceneEl.camera, window.innerWidth, window.innerHeight );
      // gtaoPass.output = GTAOPass.OUTPUT.Default;
      // gtaoPass.thickness = 10;
      // gtaoPass.scale = 2;

      // const ssaoPass = new SSAOPass( sceneEl.object3D, sceneEl.camera, window.innerWidth, window.innerHeight );
      // ssaoPass.output = SSAOPass.OUTPUT.Default;
      // ssaoPass.kernelRadius = 128;
      // ssaoPass.minDistance = 0.001;
      // ssaoPass.maxDistance = 3;

      const composer = new EffectComposer( renderer );
      composer.addPass( renderScene );
      composer.addPass( bloomPass );
      // composer.addPass( gtaoPass );
      // composer.addPass( ssaoPass );
      // composer.addPass( dotScreenPass );
      // composer.addPass( bokehPass );
      composer.addPass( outputPass );

      let isDigest = false;
      renderer.render = function () {
        if (isDigest) {
          render.apply(renderer, arguments);
        } else {
          isDigest = true;
          composer.render();
          isDigest = false;
        }
      };

      // const postprocessing = {};
      // postprocessing.composer = composer;
			// postprocessing.bokeh = bokehPass;

      // const effectController = {
      //   focus: 1,
      //   aperture: 1,
      //   maxblur: 0.01
      // };

      // const matChanger = function ( ) {
      //   postprocessing.bokeh.uniforms[ 'focus' ].value = effectController.focus;
      //   postprocessing.bokeh.uniforms[ 'aperture' ].value = effectController.aperture * 0.00001;
      //   postprocessing.bokeh.uniforms[ 'maxblur' ].value = effectController.maxblur;
      // };

      // //GUI
      // const gui = new GUI();
			// gui.add( effectController, 'focus', 1.0, 3000.0, 1 ).onChange( matChanger );
			// gui.add( effectController, 'aperture', 0, 10, 1 ).onChange( matChanger );
			// gui.add( effectController, 'maxblur', 0.0, 0.01, 0.001 ).onChange( matChanger );

      

      // WORLD
      let timeStep = 1/60;
      let lastCallTime = performance.now() / 1000;
      const physicsWorld = new CANNON.World({ gravity: new CANNON.Vec3(0, -9.81 * 1.8, 0) });
      physicsWorld.gravity.set(0, -9.8*2, 0);//
      const qmimiMaterial = new CANNON.Material('qmimi');
      const groundMaterial = new CANNON.Material('ground');
      const qmimi_ground = new CANNON.ContactMaterial(qmimiMaterial, groundMaterial, {
        friction: 0.6,
        restitution: 0,
        contactEquationStiffness: 1000
      })
      physicsWorld.addContactMaterial(qmimi_ground);

      //  CANNON DEBUGGER
      // let cannonDebugger = new CannonDebugger(sceneEl.object3D, physicsWorld, {
      //   color:0xff0000
      // });

      // GROUND
      // const groundCannon = document.querySelector('#cannon');
      const groundBody = new CANNON.Body({
        material: groundMaterial,
        mass: 0,
        friction: 0.6,
        position: new CANNON.Vec3(0,0,0),
        quaternion: new CANNON.Quaternion(0,0,0),
        type: CANNON.BODY_TYPES.STATIC
      });
      
      let gltfLoader = new GLTFLoader();
      gltfLoader.load('models/cannon.glb', function(gltf){
        gltf.scene.traverse( function ( object ) {
          if ( object.isMesh ){
            groundBody.addShape(CannonUtils.CreateTrimesh(object.geometry));
          }
        });
      });
      physicsWorld.addBody(groundBody);


      // LIGHT
      const basicLight = document.querySelector('#basic_light');

      // QMIMI & BODY
      const qmimi = document.querySelector('#qmimi');
      const qmimiBody = new CANNON.Body({
        material: qmimiMaterial,
        mass: 1,
        friction: 1,
        angleDamping: 1,
        linearDamping: 0.8,
        position: new CANNON.Vec3(0,2,96),
        type: CANNON.BODY_TYPES.DYNAMIC
      });
      qmimiBody.addShape(new CANNON.Sphere(0.8));
      physicsWorld.addBody(qmimiBody);
      
      // CAR & BODY - CAR
      const maxSteerVal = 0.8;
      const maxForce = 1000;
      // let brakeForce = 0;
      // let carFrontForce, carFrontBack, carSteerValLeft, carSteerValRight;
      const car = document.querySelector('#car');
      const carBody = new CANNON.Body({
        mass: 200,
        position: new CANNON.Vec3(8,2,93.5),
        // quaternion: new CANNON.Quaternion(0,Math.PI/10 + Math.PI,0)
      });
      // carBody.addShape(new CANNON.Box(new CANNON.Vec3(1.75, 0.8, 1)));
      // carBody.addShape(new CANNON.Sphere(1.2), new CANNON.Vec3(-0.3, 2.4, 0));
      // carBody.addShape(new CANNON.Sphere(1.2), new CANNON.Vec3(0.8, 0.4, 0));
      // carBody.addShape(new CANNON.Sphere(1.2), new CANNON.Vec3(-0.9, 0.4, 0));
      carBody.addShape(new CANNON.Sphere(1.2), new CANNON.Vec3(0.3, 2.0, 0));
      carBody.addShape(new CANNON.Sphere(1.2), new CANNON.Vec3(0.8, 0.2, 0));
      carBody.addShape(new CANNON.Sphere(1.2), new CANNON.Vec3(-0.9, 0.2, 0));

      let vehicle = new CANNON.RaycastVehicle({
        chassisBody: carBody
      });

      // WHEEL SETTING
      let wheelOptions = {
        radius: 0.4,
        directionLocal: new CANNON.Vec3(0, -1, 0),
        suspensionStiffness: 30,
        suspensionRestLength: 0.3,
        frictionSlip: 1.4,
        dampingRelaxation: 2.3,
        dampingCompression: 4.4,
        maxSuspensionForce: 100000,
        rollInfluence: 0.01,
        axleLocal: new CANNON.Vec3(0, 0, 1),
        chassisConnectionPointLocal: new CANNON.Vec3(-1, 0, 1),
        maxSuspensionTravel: 0.3,
        customSlidingRotationalSpeed: -30,
        useCustomSlidingRotationalSpeed: true,
      }
      let wheelBodies = [];
      let wheelObjects = [document.querySelector('#car_wheel_fr').object3D, document.querySelector('#car_wheel_fl').object3D, document.querySelector('#car_wheel_br').object3D, document.querySelector('#car_wheel_bl').object3D];
      
      let wx = 1.3;
      let wy = -0.6;
      let wz = 0.9;
      wheelOptions.chassisConnectionPointLocal.set(-wx, wy, wz);
      vehicle.addWheel(wheelOptions);
      wheelOptions.chassisConnectionPointLocal.set(-wx, wy, -wz);
      vehicle.addWheel(wheelOptions);
      wheelOptions.chassisConnectionPointLocal.set(wx, wy, wz);
      vehicle.addWheel(wheelOptions);
      wheelOptions.chassisConnectionPointLocal.set(wx, wy, -wz);
      vehicle.addWheel(wheelOptions);
      vehicle.addToWorld(physicsWorld);

      vehicle.wheelInfos.forEach((wheel) => {
        let cylinderShape = new CANNON.Cylinder(wheel.radius, wheel.radius, wheel.radius, 18);
        let wheelBody = new CANNON.Body({
            mass: 0,
            material: qmimiMaterial,
        });
        wheelBody.type = CANNON.Body.KINEMATIC;
        wheelBody.collisionFilterGroup = 0; // turn off collisions
        // let wheel_quaternion = new CANNON.Quaternion().setFromEuler(-Math.PI / 2, 0, 0);
        let wheel_quaternion = new CANNON.Quaternion().setFromEuler(-Math.PI / 2, 0, 0);
        wheelBody.addShape(cylinderShape, new CANNON.Vec3(), wheel_quaternion);
        wheelBodies.push(wheelBody);
        physicsWorld.addBody(wheelBody);
      })
    
      // 
      const driveConstraint = new CANNON.PointToPointConstraint(
        qmimiBody, // 
        new CANNON.Vec3(0, 0, 0), // 
        carBody, // 
        new CANNON.Vec3(0, 0, 0) // 
      );

      // goDrive();
      // goWalk();

      
      
      let clock = new THREE.Clock();
      // 
      let forceFront = 0 ,forceBack = 0, forceLeft = 0, forceRight = 0;
      const forceNumber = 24;
      // 
      let animateNow = 'Stand';
      let qmimi_mixer, qmimi_mesh, qmimi_clips
      let standAction, walkAction, runAction, jumpAction, driveAction
      let nowStandWeight = 1, nowWalkWeight = 0, nowRunWeight = 0, nowJumpWeight = 0, nowDriveWeight = 0;
      let goStandWeight = 1, goWalkWeight = 0, goRunWeight = 0, goJumpWeight = 0, goDriveWeight = 0;
      let mixerUpdateDelta;
      let qmimi_speed = 0;//QMIMI
      
      qmimi.addEventListener('model-loaded', e => {
        // 
        qmimi_mesh = qmimi.components['gltf-model'].model.children[0];  
        // 
        qmimi_clips = qmimi.components['gltf-model'].model.animations;
        qmimi_mixer = new THREE.AnimationMixer(qmimi_mesh);
        // 
        for (let i = 0; i < qmimi_clips.length; i++) {
          if(qmimi_clips[i].name == 'Run'){ runAction = qmimi_mixer.clipAction(qmimi_clips[i]); }
          else if(qmimi_clips[i].name == 'Walk'){ walkAction = qmimi_mixer.clipAction(qmimi_clips[i]); }
          else if(qmimi_clips[i].name == 'Stand'){ standAction = qmimi_mixer.clipAction(qmimi_clips[i]); }
          else if(qmimi_clips[i].name == 'Jump'){ jumpAction = qmimi_mixer.clipAction(qmimi_clips[i]); }
          else if(qmimi_clips[i].name == 'Drive'){ driveAction = qmimi_mixer.clipAction(qmimi_clips[i]); }
        }
        standAction.play();
        walkAction.play();
        runAction.play();
        driveAction.play();
         
        // 
        updatePosition();
      });
      
      function goDrive(){
        controlMode = "CAR";
        let qPosition = new CANNON.Vec3(carBody.position.x, carBody.position.y + 0.1, carBody.position.z);
        qmimiBody.position.copy(qPosition);
        physicsWorld.addConstraint(driveConstraint); // 
      }

      function goWalk(){
        controlMode = "QMIMI";
        physicsWorld.removeConstraint(driveConstraint); // 
        let qPosition = new CANNON.Vec3(carBody.position.x, carBody.position.y + 2, carBody.position.z);
        qmimiBody.position.copy(qPosition);
      }

      // KEYDOWN
      document.addEventListener('keydown', function(event) {
        // 
        if(controlMode == "QMIMI"){
          switch (event.key) {
            case 'w':
              forceFront = forceNumber;
              break;
            case 's':
              forceBack = -forceNumber;
              break;
            case 'a':
              forceLeft = forceNumber;
              break;
            case 'd':
              forceRight = -forceNumber;
              break;
            case ' ':
              if(animateNow !== 'Jump'){ qmimiJump(); }
              break;
            case 'q':
              goDrive();
              break;
          }
        }else if(controlMode == "CAR"){
          switch (event.key) {
            case 'w':
              // carFrontForce = maxForce;
              vehicle.applyEngineForce(-maxForce, 2);
              vehicle.applyEngineForce(-maxForce, 3);
              break;
            case 's':
              // carFrontBack = -maxForce;
              vehicle.applyEngineForce(maxForce, 2);
              vehicle.applyEngineForce(maxForce, 3);
              break;
            case 'a':
              // carSteerValLeft = maxSteerVal;
              vehicle.setSteeringValue(maxSteerVal, 0);
              vehicle.setSteeringValue(maxSteerVal, 1);
              break;
            case 'd':
              // carSteerValRight = -maxSteerVal;
              vehicle.setSteeringValue(-maxSteerVal, 0);
              vehicle.setSteeringValue(-maxSteerVal, 1);
              break;
            case 'b':
              // brakeForce = 10000;
              vehicle.setBrake(1000, 0);
              vehicle.setBrake(1000, 1);
              vehicle.setBrake(1000, 2);
              vehicle.setBrake(1000, 3);
              break;
            case 'q':
              goWalk();
              break;
          }
          // carMove();
        }
      });

      // KEYUP
      document.addEventListener('keyup', function(event) {
        // 
        if(controlMode == "QMIMI"){
          switch (event.key) {
            case 'w':
              forceFront = 0;
              break;
            case 's':
              forceBack = 0;
              break;
            case 'a':
              forceLeft = 0;
              break;
            case 'd':
              forceRight = 0;
              break;
          }
        }else if(controlMode == "CAR"){
          switch (event.key) {
            case 'w':
              // carFrontForce = 0;
              vehicle.applyEngineForce(0, 2);
              vehicle.applyEngineForce(0, 3);
              break;
            case 's':
              // carFrontBack = 0;
              vehicle.applyEngineForce(0, 2);
              vehicle.applyEngineForce(0, 3);
              break;
            case 'a':
              // carSteerValLeft = 0;
              vehicle.setSteeringValue(0, 0);
              vehicle.setSteeringValue(0, 1);
              break;
            case 'd':
              // carSteerValRight = 0;
              vehicle.setSteeringValue(0, 0);
              vehicle.setSteeringValue(0, 1);
              break;
            case 'b':
              // brakeForce = 0;
              vehicle.setBrake(0, 0);
              vehicle.setBrake(0, 1);
              vehicle.setBrake(0, 2);
              vehicle.setBrake(0, 3);
              break;
          }
          // carMove();
        }
      });

      window.addEventListener( 'resize', onWindowResize );
      function onWindowResize() {
				const width = window.innerWidth;
				const height = window.innerHeight;
				composer.setSize( width, height );
			}
      onWindowResize();
      
      //
      let robot_ear = document.querySelector('#robot_ear').object3D;
      let airship_ear = document.querySelector('#airship_ear').object3D;
      let ufo = document.querySelectorAll('#ufo');
      let Ferris_wheel = document.querySelector('#Ferris_wheel').object3D;
      let fw_ball = document.querySelectorAll('#fw_ball');
      let step_light = document.querySelectorAll('#step_light');
      let step_light_number = 0;
      
      //Sequin
      let airship = document.querySelector('#airship').object3D;
        
      for (let i = 0; i < 300; i++) {
        let _sequin = new BIG.Sequin(airship);
        sceneEl.object3D.add(_sequin);
        sequinArray.push(_sequin);
      }
      

      //  requestAnimationFrame 
      function updatePosition() {
        let qmimiPosition = new THREE.Vector3(qmimiBody.position.x, qmimiBody.position.y-0.8, qmimiBody.position.z);
        qmimi.object3D.position.copy(qmimiPosition);
        
        let carPosition = new THREE.Vector3(carBody.position.x, carBody.position.y, carBody.position.z);
        car.object3D.position.copy(carBody.position);
        car.object3D.quaternion.copy(carBody.quaternion);

        // orbit-controls
        let targetX, targetY, targetZ;
        if(controlMode == "QMIMI"){
          targetX = qmimiPosition.x;
          targetY = qmimiPosition.y + 5;
          targetZ = qmimiPosition.z;
        }else{
          targetX = carPosition.x;
          targetY = carPosition.y + 5;
          targetZ = carPosition.z;
        }
        basicCamera.setAttribute('orbit-controls', 'target', targetX + ' ' + targetY + ' ' + targetZ);
        
        // LIGHT
        basicLight.object3D.position.set(targetX - 30, targetY + 30, targetZ - 30);
        // redLight.object3D.position.set(targetX + 30, targetY + 30, targetZ + 30);
        // blueLight.object3D.position.set(targetX - 30, targetY + 30, targetZ - 30);

        if(controlMode == "QMIMI"){
          // 
          let goRY = Math.atan2(qmimiBody.position.x - document.querySelector('#basic_camera').getObject3D('camera').position.x, qmimiBody.position.z - document.querySelector('#basic_camera').getObject3D('camera').position.z);
          // 
          let euler = new THREE.Euler(0, goRY, 0, 'XYZ');
          let force = new THREE.Vector3(forceLeft + forceRight, 0 , forceFront + forceBack );
          force.applyEuler(euler);
          // 

          qmimi_speed = Math.sqrt(Math.pow(qmimiBody.velocity.x, 2) + Math.pow(qmimiBody.velocity.z, 2));
          if (qmimiBody) { // BODY
            let transformedForce = new CANNON.Vec3(force.x, force.y, force.z);
            qmimiBody.applyForce(transformedForce, new CANNON.Vec3());
            if(qmimi_speed > 0.03){
              const zeroQuaternion = new THREE.Quaternion(); // 
              //  quaternion 
              qmimi.object3D.quaternion.set(zeroQuaternion.x, zeroQuaternion.y, zeroQuaternion.z, zeroQuaternion.w);
              
              let RY = Math.atan2(qmimiBody.velocity.x, qmimiBody.velocity.z);
              qmimi.object3D.rotation.y = RY;
            }
          }
        }else{
          qmimi.object3D.quaternion.copy(car.object3D.quaternion);
        }

        setAnimation();
        
        // STATS
        // stats.update();

        // CannonDebugger
        // cannonDebugger.update();

        // CAR
        for (let i = 0; i < vehicle.wheelInfos.length; i++) {
          vehicle.updateWheelTransform(i);
          let transform = vehicle.wheelInfos[i].worldTransform;
          let wheelBody = wheelBodies[i];
          wheelBody.position.copy(transform.position);
          wheelBody.quaternion.copy(transform.quaternion);
          wheelObjects[i].position.copy(transform.position);
          wheelObjects[i].quaternion.copy(transform.quaternion);
        }

        // WORLD
        let time = performance.now() / 1000;
        let dt = time - lastCallTime;
        lastCallTime = time;
        physicsWorld.step(timeStep, dt);

        


        //
        //ROBOT AIRSHIP EAR
        let earRX = Math.sin(time)/3;
        robot_ear.rotation.x = earRX;
        airship_ear.rotation.x = earRX;
        //UFO
        ufo.forEach(function(data, i){
          data.object3D.position.x = 80 + 15 * Math.sin(time/2 + i*Math.PI*2/6);
          data.object3D.position.z = 46.5 + 15 * Math.cos(time/2 + i*Math.PI*2/6);
          data.object3D.rotation.y = (Math.atan2(Math.sin(time/2), Math.cos(time/2))+ i*Math.PI*2/6 + Math.PI);
          data.object3D.position.y = 3 + 6 * Math.sin((time/2 + i*Math.PI*2/6)*2)/2;
        });
        //fw_ball
        Ferris_wheel.rotation.z = -Math.atan2(Math.sin(time/24), Math.cos(time/24));
        fw_ball.forEach(function(data, i){
          data.object3D.position.x = 0 + 29 * Math.sin(time/24 + i * Math.PI*2/6);
          data.object3D.position.y = 60 + 29 * Math.cos(time/24 + i * Math.PI*2/6);
          data.object3D.position.z = -133;
        });

        //step_light
        step_light.forEach(function(data, i){
          let _scale = data.object3D.scale.x;
          if(_scale > 4.99){
            if(_scale != 5){
              data.object3D.visible = false;
              _scale = 5;
              data.object3D.scale.set(_scale, _scale, _scale);
              data.setAttribute('material', 'opacity', 5-_scale);
            }
          }else{
            if(data.object3D.visible != true){
              data.object3D.visible = true;
            }
            _scale = (5 - _scale)/36 + _scale;
            data.object3D.scale.set(_scale, _scale, _scale);
            data.setAttribute('material', 'opacity', 5-_scale);
          }
        });

        //AirShip
        airship.position.x = 0 + 60 * Math.cos(time/18);
        airship.position.z = 30 + 60 * Math.sin(time/18);
        airship.position.y = 30;
        airship.rotation.y = (Math.atan2(Math.cos(time/18), Math.sin(time/18)) - Math.PI/2);

        for (let i = 0; i < sequinArray.length; i++) {
          sequinArray[i].update();
        }

        // 
        requestAnimationFrame(updatePosition);
      }

      let slightNow = false;

      // QMIMI
      function setAnimation(){
        if(controlMode == "CAR"){
          animateNow = "Drive";
        }else{
          if(animateNow !== 'Jump'){
            if(qmimi_speed < 1){
              // Stand
              if(animateNow !== "Stand"){ animateNow = "Stand"; }
            }else if(qmimi_speed > 3){
              // Run
              if(animateNow !== "Run"){ animateNow = "Run"; }
            }else{
              // Walk
              if(animateNow !== "Walk"){ animateNow = "Walk"; }
            }
          }
        }
        
        if(animateNow == "Stand"){
          goStandWeight = 1;
          goWalkWeight = 0;
          goRunWeight = 0;
          goJumpWeight = 0;
          goDriveWeight = 0;
        }else if(animateNow == "Walk"){
          goStandWeight = 0;
          goWalkWeight = 1;
          goRunWeight = 0;
          goJumpWeight = 0;
          goDriveWeight = 0;
        }else if(animateNow == "Run"){
          goStandWeight = 0;
          goWalkWeight = 0;
          goRunWeight = 1;
          goJumpWeight = 0;
          goDriveWeight = 0;
        }else if(animateNow == "Jump"){
          goStandWeight = 0;
          goWalkWeight = 0;
          goRunWeight = 0;
          goJumpWeight = 1;
          goDriveWeight = 0;
        }else if(animateNow == "Drive"){
          goStandWeight = 0;
          goWalkWeight = 0;
          goRunWeight = 0;
          goJumpWeight = 0;
          goDriveWeight = 1;
        }
        // 
        mixerUpdateDelta = clock.getDelta();
        // Update the animation mixer, the stats panel, and render this frame
        qmimi_mixer.update( mixerUpdateDelta );

        // 
        let _time = qmimi_mixer.time;
        var decimalPart = _time - Math.floor(_time);
        
        // 
        
        if(animateNow == "Run" || animateNow == "Walk"){
          if ((decimalPart >= 0.316 && decimalPart <= 0.35) || (decimalPart >= 0.816 && decimalPart <= 0.85)) {
            if(slightNow == false){
              slightNow = true;
              step_light_out();
            }
          }else{
            slightNow = false;
          }
        }
        

        if(Math.abs(goRunWeight - nowRunWeight) < 0.02){ nowRunWeight = goRunWeight; }
        else{ nowRunWeight = nowRunWeight + (goRunWeight - nowRunWeight)/10; }

        if(Math.abs(goWalkWeight - nowWalkWeight) < 0.02){ nowWalkWeight = goWalkWeight; }
        else{ nowWalkWeight = nowWalkWeight + (goWalkWeight - nowWalkWeight)/10; }

        if(Math.abs(goStandWeight - nowStandWeight) < 0.02){ nowStandWeight = goStandWeight; }
        else{ nowStandWeight = nowStandWeight + (goStandWeight - nowStandWeight)/10; }

        if(Math.abs(goJumpWeight - nowJumpWeight) < 0.02){ nowJumpWeight = goJumpWeight; }
        else{ nowJumpWeight = nowJumpWeight + (goJumpWeight - nowJumpWeight)/5; }

        if(Math.abs(goDriveWeight - nowDriveWeight) < 0.02){ nowDriveWeight = goDriveWeight; }
        else{ nowDriveWeight = nowDriveWeight + (goDriveWeight - nowDriveWeight)/5; }
        
        runAction.setEffectiveWeight(nowRunWeight);
        walkAction.setEffectiveWeight(nowWalkWeight);
        standAction.setEffectiveWeight(nowStandWeight);
        jumpAction.setEffectiveWeight(nowJumpWeight);
        driveAction.setEffectiveWeight(nowDriveWeight);
      }

      function step_light_out(){
        step_light[step_light_number].object3D.position.set(qmimiBody.position.x, qmimiBody.position.y-0.8, qmimiBody.position.z);
        step_light[step_light_number].object3D.scale.set(0.1,0.1,0.1);
        if(step_light_number == 5){
          step_light_number = 0;
        }else{
          step_light_number++;
        }
      }
      // QMOMO JUMP
      function qmimiJump(){
        if(animateNow !== 'Jump'){
          step_light_out();
          let _qmimiBody = qmimiBody;
          animateNow = 'Jump';
          // setAnimation();
          setTimeout(function(){
            jumpAction.stop();
            jumpAction.play();
            _qmimiBody.type = CANNON.Body.DYNAMIC;
            let force = new CANNON.Vec3(0, 800, 0);
            let point = new CANNON.Vec3(0, 0, 0);
            _qmimiBody.applyForce(force, point);
          }, 60);
          setTimeout(function(){
            animateNow = 'Run';
            // setAnimation();
          }, 1000);
        }
      }
    }
});


